import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { getToken } from '@/utils/auth' // 
import { Message } from 'element-ui'

// permissiom judge
function hasPermission(roles, permissionRoles) {
  if (roles.indexOf('admin') >= 0) return true // admin 
  if (!permissionRoles) return true
  return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

const whiteList = ['/login', '/authredirect']// 

router.beforeEach((to, from, next) => {
  NProgress.start() // Progress
  if (getToken()) { // token
    if (to.path === '/login') {
      next({ path: '/' })
      NProgress.done() // routerhash hash  afterEach hack pshistory
    } else {
      if (store.getters.roles.length === 0) { // user_info
        store.dispatch('GetUserInfo').then(res => { // user_info
          const roles = res.data.role
          store.dispatch('GenerateRoutes', { roles }).then(() => { // 
            router.addRoutes(store.getters.addRouters) // 
            next({ ...to, replace: true }) // hack addRoutes ,replace: true so the navigation will not leave a history record
          })
        }).catch(() => {
          store.dispatch('FedLogOut').then(() => {
            Message.error(',')
            next({ path: '/login' })
          })
        })
      } else {
        // next()  
        if (hasPermission(store.getters.roles, to.meta.role)) {
          next()//
        } else {
          next({ path: '/401', query: { noGoBack: true }})
          NProgress.done() // routerhash hash  afterEach hack pshistory
        }
        //  
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) { // 
      next()
    } else {
      next('/login') // 
      NProgress.done() // routerhash hash  afterEach hack pshistory
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})



